.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 144rem;
    padding: 1.5rem 0;
}

.nav {
    display: flex;
    align-items: center;
    list-style: none;
    // padding-left: 17.5rem;
    &-item  {
         margin-right: 5.4rem;;
        }
    &-link {
        text-decoration: none;
        font-weight: 700;
        color: rgba($white, 0.5);
        transition: 0.2s ease color;
        &:hover {
            color: rgba($white, 1);
        }
        &::after {
            content: '';
            opacity: 0;
            display: block;
            width: 100%;
            height: .2rem;
            margin-top: .5rem;
            border-radius: 5rem;
            background: $bg-gradient-safe;
            background: $bg-gradient;        
            }
            &.active {
                color: rgba($white, 1);
                &::after {
                    opacity: 1;
                }
            } 
    }
}

.control {
    display: flex;
    align-items: center;
    &-item {
        margin-right: 7.5rem;
        list-style: none;
    }
    &-item:last-child {
         margin-right: 0;
        }
         
    &-btn {
        background-color: transparent;
        border: none;
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        &_search {
            background-image: url(../images/search.svg);
            width: 1.8rem;
            height: 1.8rem;
        }
        &_notice {
            background-image: url(../images/notice.svg);
            width: 2rem;
            height: 2rem;
        }
        &_account {
            background-image: url(../images/account.svg);
            width: 1.8rem;
            height: 1.9rem;
        }
    }
}
.burger {
    display: none;
    position: absolute;
    top: 2.5rem;
    right: 2rem;
    z-index: 11;
    width: 4rem;
    height: 2.8rem;
    border: 0;
    background: 0 0;
    padding: 0;
}